import { useEffect, useState } from "react";
import { Container, Grid, Card, CardContent, Typography, Divider, Box } from "@mui/material";
import { bankPublic } from "../../../api/login/Login";
// import "bootstrap/dist/css/bootstrap.min.css";

function CompanyBankDetails() {
    const [data, setData] = useState([]);

    const detailsBank = async () => {
        try {
            const res = await bankPublic();
            setData(res?.data?.data || []);
        } catch (error) {
            console.error("Error fetching bank details", error);
        }
    };

    useEffect(() => {
        detailsBank();
    }, []);

    return (
        <Container>
            <Typography 
                variant="h4" 
                gutterBottom 
                className="text-center mt-4"
                sx={{ fontWeight: "bold", color: "#2c3e50" , mb:4 }}
            >
                Company Bank Details
            </Typography>
            <Grid container spacing={4}>
                {data.map((item, index) => (
                    <Grid item xs={12} sm={6} md={4} key={index}>
                        <Card 
                            sx={{ 
                                background: "linear-gradient(135deg, #ece9e6 0%, #ffffff 100%)", 
                                borderRadius: 3, 
                                boxShadow: 3, 
                                transition: "0.3s",
                                '&:hover': {
                                    transform: "scale(1.05)",
                                    boxShadow: 6,
                                },
                            }}
                        >
                            <CardContent>
                                <Box sx={{ textAlign: "center", mb: 2 }}>
                                    <Typography variant="h6" sx={{ fontWeight: "bold", color: "#34495e" }}>
                                        {item.bank_name}
                                    </Typography>
                                    <Divider sx={{ my: 1, backgroundColor: "#3498db" }} />
                                </Box>
                                <Typography variant="body1" sx={{ fontSize: "1rem", fontWeight: "500" }}>
                                    <strong>Account Number:</strong> {item.bank_account_number}
                                </Typography>
                                <Typography variant="body1" sx={{ fontSize: "1rem", fontWeight: "500" }}>
                                    <strong>IFSC Code:</strong> {item.ifsc_code}
                                </Typography>
                                <Typography variant="body1" sx={{ fontSize: "1rem", fontWeight: "500" }}>
                                    <strong>Bank Code:</strong> {item.bank_code}
                                </Typography>
                                <Typography variant="body1" sx={{ fontSize: "1rem", fontWeight: "500" }}>
                                    <strong>Branch Name:</strong> {item.branch_name}
                                </Typography>
                                {item.remark && (
                                    <Typography 
                                        variant="body2" 
                                        sx={{ mt: 2, fontStyle: "italic", color: "#7f8c8d", fontWeight: "500" }}
                                    >
                                        <strong>Remarks:</strong> {item.remark}
                                    </Typography>
                                )}
                            </CardContent>
                        </Card>
                    </Grid>
                ))}
            </Grid>
        </Container>
    );
}

export default CompanyBankDetails;
